.features .box {
  padding: 50px;
}
.features .box h2 {
  margin: 0px 0 15px 0;
}
.features .box p {
  word-spacing: 2px;
  transition: 0.5s;
}
.features .box i {
  font-size: 30px;
  margin-top: 15px;
  transition: 0.5s;
  opacity: 0;
}
.features .box:hover p {
  color: white;
}
.features .box:hover i {
  color: white;
  opacity: 1;
}

.features .box h5 {
  margin-top: 5px;
  text-align: center;
}
@media (max-width: 768px){
.box btn-shadow{
  display:block;
}





}